import React, { useState, useEffect } from 'react';
import { Empresa } from '@/services/api/empresa.service';
import { Plano } from '@/services/api/plano.service';
import { services } from '@/services/api';
import Input from '@/components/form/input/InputField';
import Select, { SelectOption } from '@/components/ui/Select';
import Button from '@/components/ui/button/Button';
import Label from '@/components/form/Label';
import toast from 'react-hot-toast';

type EmpresaFormProps = {
  empresa?: Empresa | null;
  onSubmit: (data: any) => void;
  onCancel: () => void;
};

const EmpresaForm: React.FC<EmpresaFormProps> = ({
  empresa,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState({
    nome: '',
    cpfCnpj: '',
    celular: '',
    cep: '',
    cidade: '',
    estado: '',
    bairro: '',
    rua: '',
    numero: '',
    vencimento: '',
    planoId: ''
  });
  const [planos, setPlanos] = useState<Plano[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadPlanos();
    if (empresa) {
      setFormData({
        nome: empresa.nome || '',
        cpfCnpj: empresa.cpfCnpj || '',
        celular: empresa.celular || '',
        cep: empresa.cep || '',
        cidade: empresa.cidade || '',
        estado: empresa.estado || '',
        bairro: empresa.bairro || '',
        rua: empresa.rua || '',
        numero: empresa.numero || '',
        vencimento: empresa.vencimento ? empresa.vencimento.split('T')[0] : '',
        planoId: ''
      });
    }
  }, [empresa]);

  const loadPlanos = async () => {
    try {
      const response = await services.plano.list({ perPage: 1000 });
      setPlanos(response.data || []);
    } catch (error) {
      toast.error('Erro ao carregar planos');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const data = {
        ...formData,
        vencimento: new Date(formData.vencimento).toISOString(),
        planoId: formData.planoId ? parseInt(formData.planoId) : undefined
      };
      onSubmit(data);
    } catch (error) {
      toast.error('Erro ao processar formulário');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const planoOptions: SelectOption[] = planos.map(plano => ({
    value: plano.id.toString(),
    label: `${plano.nome} - ${plano.valor.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}`
  }));

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label htmlFor="nome">Nome da Empresa *</Label>
          <Input
            id="nome"
            value={formData.nome}
            onChange={(e) => handleChange('nome', e.target.value)}
            required
            placeholder="Digite o nome da empresa"
          />
        </div>
        <div>
          <Label htmlFor="cpfCnpj">CPF/CNPJ</Label>
          <Input
            id="cpfCnpj"
            value={formData.cpfCnpj}
            onChange={(e) => handleChange('cpfCnpj', e.target.value)}
            placeholder="Digite o CPF ou CNPJ"
          />
        </div>
        <div>
          <Label htmlFor="celular">Celular</Label>
          <Input
            id="celular"
            value={formData.celular}
            onChange={(e) => handleChange('celular', e.target.value)}
            placeholder="Digite o celular"
          />
        </div>
        <div>
          <Label htmlFor="cep">CEP</Label>
          <Input
            id="cep"
            value={formData.cep}
            onChange={(e) => handleChange('cep', e.target.value)}
            placeholder="Digite o CEP"
          />
        </div>
        <div>
          <Label htmlFor="cidade">Cidade</Label>
          <Input
            id="cidade"
            value={formData.cidade}
            onChange={(e) => handleChange('cidade', e.target.value)}
            placeholder="Digite a cidade"
          />
        </div>
        <div>
          <Label htmlFor="estado">Estado</Label>
          <Input
            id="estado"
            value={formData.estado}
            onChange={(e) => handleChange('estado', e.target.value)}
            placeholder="Digite o estado"
          />
        </div>
        <div>
          <Label htmlFor="bairro">Bairro</Label>
          <Input
            id="bairro"
            value={formData.bairro}
            onChange={(e) => handleChange('bairro', e.target.value)}
            placeholder="Digite o bairro"
          />
        </div>
        <div>
          <Label htmlFor="rua">Rua</Label>
          <Input
            id="rua"
            value={formData.rua}
            onChange={(e) => handleChange('rua', e.target.value)}
            placeholder="Digite a rua"
          />
        </div>
        <div>
          <Label htmlFor="numero">Número</Label>
          <Input
            id="numero"
            value={formData.numero}
            onChange={(e) => handleChange('numero', e.target.value)}
            placeholder="Digite o número"
          />
        </div>
        <div>
          <Label htmlFor="vencimento">Data de Vencimento *</Label>
          <Input
            id="vencimento"
            type="date"
            value={formData.vencimento}
            onChange={(e) => handleChange('vencimento', e.target.value)}
            required
          />
        </div>
        <div>
          <Label htmlFor="planoId">Plano</Label>
          <Select
            placeholder="Selecione um plano"
            options={planoOptions}
            value={planoOptions.find(opt => opt.value === formData.planoId)}
            onChange={(option) => handleChange('planoId', ((option as any)?.value))}
            isClearable
          />
        </div>
      </div>

      <div className="flex justify-end gap-3 pt-4">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          disabled={loading}
        >
          Cancelar
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={loading}
        >
          {loading ? 'Salvando...' : empresa ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default EmpresaForm;
