'use client';

import React, { useCallback, useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { services, User } from '@/services/api';
import { Empresa } from '@/services/api/empresa.service';
import { formatCurrency, formatDate } from '@/utils/format';
import { 
  BuildingOfficeIcon, 
  PlusIcon,
  MagnifyingGlassIcon
} from '@heroicons/react/24/outline';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/common/Pagination';
import { Modal } from '@/components/ui/modal';
import EmpresaCard from '@/components/admin/EmpresaCard';
import EmpresaForm from '@/components/admin/EmpresaForm';
import Label from '@/components/form/Label';
import toast from 'react-hot-toast';
import { useAuth } from '@/hooks/useAuth';

export default function EmpresasManagement() {
  const router = useRouter();
  const [empresas, setEmpresas] = useState<Empresa[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(9);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedEmpresa, setSelectedEmpresa] = useState<Empresa | null>(null);
  const { logout } = useAuth();

  
  const loadEmpresas = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.empresa.list({
        page: currentPage,
        perPage: itemsPerPage,
        search: searchTerm || undefined
      });
      
      setEmpresas(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar empresas:', error);
      toast.error('Erro ao carregar empresas');
    } finally {
      setLoading(false);
    }
  }, [currentPage, itemsPerPage, searchTerm]);

  useEffect(() => {
    loadEmpresas();
  }, [loadEmpresas]);

  const handleEdit = (empresa: Empresa) => {
    setSelectedEmpresa(empresa);
    setIsModalOpen(true);
  };

  type Usuario = User & { token?: string };
  type Company = Empresa & { usuarios: Usuario[] };
  const handleView = (empresa: Company) => {
    // Implementar visualização detalhada
    const userAdmin = empresa.usuarios.find((usuario: Usuario) => usuario.role === 'ADMIN' && usuario.token);
    if (userAdmin) {
      logout(false);
      window.location.href = `/auth/login/${userAdmin.token}`;
    } else {
      toast.error('Nenhum usuário admin encontrado');
    }
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedEmpresa) {
        await services.empresa.update(selectedEmpresa.id, data);
        toast.success('Empresa atualizada com sucesso');
      } else {
        await services.empresa.create(data);
        toast.success('Empresa criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedEmpresa(null);
      loadEmpresas();
    } catch (error) {
      console.error('Erro ao salvar empresa:', error);
      toast.error('Erro ao salvar empresa');
    }
  };

  const handleDelete = async (id: number) => {
    if (!confirm('Tem certeza que deseja excluir esta empresa?')) {
      return;
    }

    try {
      await services.empresa.delete(id);
      toast.success('Empresa excluída com sucesso');
      loadEmpresas();
    } catch (error) {
      console.error('Erro ao excluir empresa:', error);
      toast.error('Erro ao excluir empresa');
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Gerenciar Empresas
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie todas as empresas do sistema
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedEmpresa(null);
                setIsModalOpen(true);
              }}
            >
              Nova Empresa
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar empresas..."
              value={searchTerm}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setCurrentPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {empresas.length > 0 ? (
            empresas.map((empresa) => (
              <EmpresaCard
                key={empresa.id}
                empresa={empresa}
                onEdit={() => handleEdit(empresa)}
                onDelete={() => handleDelete(empresa.id)}
                onView={() => handleView(empresa as Company)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma empresa encontrada
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={currentPage}
              totalPages={totalPages}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedEmpresa(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedEmpresa ? 'Editar Empresa' : 'Nova Empresa'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedEmpresa 
                ? 'Atualize as informações da empresa.' 
                : 'Preencha os dados para criar uma nova empresa.'
              }
            </p>
          </div>
          <EmpresaForm
            empresa={selectedEmpresa}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedEmpresa(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}