import React from 'react';
import { formatDate } from '@/utils/format';
import { Empresa } from '@/services/api/empresa.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';

type EmpresaCardProps = {
  empresa: Empresa;
  onEdit: () => void;
  onDelete: () => void;
  onView: () => void;
};

const getStatusColor = (vencimento: string) => {
  const now = new Date();
  const vencimentoDate = new Date(vencimento);
  const diffTime = vencimentoDate.getTime() - now.getTime();
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

  if (diffDays < 0) return 'error'; // Vencida
  if (diffDays <= 7) return 'warning'; // Vence em 7 dias ou menos
  return 'success'; // Ativa
};

const getStatusText = (vencimento: string) => {
  const now = new Date();
  const vencimentoDate = new Date(vencimento);
  const diffTime = vencimentoDate.getTime() - now.getTime();
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

  if (diffDays < 0) return 'Vencida';
  if (diffDays === 0) return 'Vence Hoje';
  if (diffDays === 1) return 'Vence Amanhã';
  if (diffDays <= 7) return `Vence em ${diffDays} dias`;
  return 'Ativa';
};

const EmpresaCard: React.FC<EmpresaCardProps> = ({
  empresa,
  onEdit,
  onDelete,
  onView
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div className="flex-1">
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {empresa.nome}
              </h3>
              <Badge
                color={getStatusColor(empresa.vencimento)}
                className="capitalize"
              >
                {getStatusText(empresa.vencimento)}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              CNPJ: {empresa.cpfCnpj || 'Não informado'}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Celular: {empresa.celular || 'Não informado'}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Vencimento: {formatDate(empresa.vencimento)}
            </p>
            {empresa.cidade && empresa.estado && (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                {empresa.cidade} - {empresa.estado}
              </p>
            )}
          </div>
          <div className="flex gap-2">
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onView}
              title="Visualizar"
              icon="eye"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar"
              icon="edit"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onDelete}
              title="Excluir"
              icon="delete"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default EmpresaCard;
